import React from 'react';
import PropTypes from 'prop-types';
import 'materialize-css/dist/css/materialize.min.css';
import {Card} from 'react-materialize';

function EachDaySchedule({day, location, hours, booth}){
  return(
      <Card className='blue-grey darken-1' textClassName='white-text' title='Card title' actions={[<a href='#'>This is a link</a>]}>
        <p>
          {day}
          <br/>
          {location}
          <br/>
          {hours}
          <br/>
          {booth}
        </p>
    </Card>
  );
}

EachDaySchedule.propTypes = {
  day: PropTypes.string.isRequired,
  location: PropTypes.string.isRequired,
  hours: PropTypes.string.isRequired,
  booth: PropTypes.string.isRequired
};

export default EachDaySchedule;



