import React from 'react';

function ProduceSearch(){
  const searchBoxStyles = {
    border: '2px solid #808080',
    borderRadius: '10',
    padding: '5',
    paddigRight: '15'
  };
  return (
    <div>
      <label for="search">Search</label>
      <input style={searchBoxStyles} value="Search"/>
    </div>
  );
}

export default ProduceSearch;



